@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

:root {
    --violet: #7C509A;
    --grey: #FAFAFA;
    --black: #333333;
    --section-padding: 50px;
    --section-margin: 100px;
}

body {
  font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, h5, p {
font-family: Montserrat;
font-style: normal;
line-height: 120%;
}

h1 {
  font-weight: bold;
font-size: 48px;
}

h2 {
  font-weight: bold;
  font-size: 36px;
}

h3 {
  font-weight: bold;
}

h4 {
  font-weight: bold;
  font-size: 16px;
}

p {
  line-height: 160%;
}
