.variant-card {
    text-align: center;
    padding: 30px;
    margin-bottom: 30px;
    border-radius: 8px;
    background-color: white;
    transition: 0.2s ease-out;
}

.variant-card:hover {
    transform: translateY(-4px);
    box-shadow: 0px 16px 64px rgba(51, 51, 51, 0.08);
}

.variant-card img {
    max-width: 200px;
    padding-bottom: 20px;
}

.variant-card h4 {
    padding-bottom: 12px;
}