.order {
    margin: var(--section-margin) 0;
}

.order p {
    color: #828282;
}

.form {
    width: 84%;
    height: 100%;
    padding: 40px;
    border-radius: 8px;
    background-color: var(--grey);
}

.product-data {
    padding-bottom: 20px;
}

select {
    width: 100%;
    padding: 14px 4px;
    border-radius: 8px;
    margin-top: 12px;
    margin-bottom: 6px;
}

input {
    width: 100%;
    padding: 14px 4px;
    border-radius: 8px;
    margin-top: 12px;
    margin-bottom: 6px;
}

.user-data {
    margin: 24px 0;
}