@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
    max-width: 100%;
    height: auto;
}

button {
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-size: 12px;
    padding: 14px 56px;
    border: none;
    border-radius: 8px;
    outline: none;
    transition: 0.2s ease-out;
}
button:hover {
    transform: translateY(-2px);
    outline: none;
    box-shadow: 0px 8px 16px rgba(124, 80, 154, 0.24);
}
button:active {
    transform: translateY(0);
    outline: none;
    box-shadow: none;
}

.btn-violet {
    background-color: var(--violet);
    color: white;
}

.btn-big {
    width: 100%;
    font-size: 16px;
}

.btn-lite {
    color: var(--violet);
    background-color: #E1D8E7;
}

.btn-lite:hover {
    box-shadow: none;
}

button:focus {
    outline: none;
}
header {
    display: flex;
    align-items: center;

    background-color: #fAfAFA;
}

header h1 {
    padding-bottom: 10px;
}

header p {
    padding-bottom: 18px;
}

.top-line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: var(--section-padding);
}

.header-center {
        padding:  var(--section-margin) 0;
}

.logo {
    width: 100px;
}

.menu ul{
    margin: auto;
}
.menu li {
    list-style: none;
    display: inline-block;
    padding-left: 24px;

}

.menu a {
    color: var(--black);
    display: inline-flex;
}

.right {
    text-align: right;
}
:root {
    --violet: #7C509A;
    --grey: #FAFAFA;
    --black: #333333;
    --section-padding: 50px;
    --section-margin: 100px;
}

body {
  font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, h5, p {
font-family: Montserrat;
font-style: normal;
line-height: 120%;
}

h1 {
  font-weight: bold;
font-size: 48px;
}

h2 {
  font-weight: bold;
  font-size: 36px;
}

h3 {
  font-weight: bold;
}

h4 {
  font-weight: bold;
  font-size: 16px;
}

p {
  line-height: 160%;
}

.feature {
    padding-bottom: 30px;
}

.icon-block {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 72px;
    height: 72px;
    margin-bottom: 20px;
    border-radius: 8px;
    background-color: var(--grey);
    transition: 0.2s ease-out;
}

.icon-block:hover {
    background-color: white;
    transform: scale(1.2) translateY(-4px);
    box-shadow: 0px 8px 16px var(--grey);


}
.features {
    padding-top: var(--section-margin);
    padding-bottom: 70px;
}
.variant-card {
    text-align: center;
    padding: 30px;
    margin-bottom: 30px;
    border-radius: 8px;
    background-color: white;
    transition: 0.2s ease-out;
}

.variant-card:hover {
    transform: translateY(-4px);
    box-shadow: 0px 16px 64px rgba(51, 51, 51, 0.08);
}

.variant-card img {
    max-width: 200px;
    padding-bottom: 20px;
}

.variant-card h4 {
    padding-bottom: 12px;
}
.product-variants {
    padding: var(--section-margin);
    margin-bottom: 60px;
    background-color: var(--grey);
}

.title {
    padding-bottom: var(--section-padding);
}
.order {
    margin: var(--section-margin) 0;
}

.order p {
    color: #828282;
}

.form {
    width: 84%;
    height: 100%;
    padding: 40px;
    border-radius: 8px;
    background-color: var(--grey);
}

.product-data {
    padding-bottom: 20px;
}

select {
    width: 100%;
    padding: 14px 4px;
    border-radius: 8px;
    margin-top: 12px;
    margin-bottom: 6px;
}

input {
    width: 100%;
    padding: 14px 4px;
    border-radius: 8px;
    margin-top: 12px;
    margin-bottom: 6px;
}

.user-data {
    margin: 24px 0;
}
.contacts {
    padding: var(--section-margin) 0;
    margin-top: var(--section-margin) 0;
    background-color: var(--grey);
}

.contacts img {
    padding-right: 10px;
    margin-bottom: 30px;
    margin-top: 8px;
}

.contacts a {
    color: var(--black);
}
