header {
    display: flex;
    align-items: center;

    background-color: #fAfAFA;
}

header h1 {
    padding-bottom: 10px;
}

header p {
    padding-bottom: 18px;
}

.top-line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: var(--section-padding);
}

.header-center {
        padding:  var(--section-margin) 0;
}

.logo {
    width: 100px;
}

.menu ul{
    margin: auto;
}
.menu li {
    list-style: none;
    display: inline-block;
    padding-left: 24px;

}

.menu a {
    color: var(--black);
    display: inline-flex;
}

.right {
    text-align: right;
}