button {
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-size: 12px;
    padding: 14px 56px;
    border: none;
    border-radius: 8px;
    outline: none;
    transition: 0.2s ease-out;
}
button:hover {
    transform: translateY(-2px);
    outline: none;
    box-shadow: 0px 8px 16px rgba(124, 80, 154, 0.24);
}
button:active {
    transform: translateY(0);
    outline: none;
    box-shadow: none;
}

.btn-violet {
    background-color: var(--violet);
    color: white;
}

.btn-big {
    width: 100%;
    font-size: 16px;
}

.btn-lite {
    color: var(--violet);
    background-color: #E1D8E7;
}

.btn-lite:hover {
    box-shadow: none;
}

button:focus {
    outline: none;
}