.feature {
    padding-bottom: 30px;
}

.icon-block {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 72px;
    height: 72px;
    margin-bottom: 20px;
    border-radius: 8px;
    background-color: var(--grey);
    transition: 0.2s ease-out;
}

.icon-block:hover {
    background-color: white;
    transform: scale(1.2) translateY(-4px);
    box-shadow: 0px 8px 16px var(--grey);


}